import { Component, …µConsole } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";

enum Sex {
  Man = "man",
  Woman = "woman"
}

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.css"]
})
export class AppComponent {
  bmiForm: FormGroup;
  result: number;

  constructor(private fb: FormBuilder) {
    this.bmiForm = this.fb.group({
      sex: [Sex.Man, [Validators.required]],
      weight: [
        null,
        [
          Validators.required,
          Validators.minLength(2),
          Validators.min(10),
          Validators.max(500)
        ]
      ],
      height: [
        null,
        [
          Validators.required,
          Validators.minLength(2),
          Validators.min(30),
          Validators.max(250)
        ]
      ]
    });
  }

  submitHandler(): void {
    const weight: number = this.bmiForm.get("weight").value;
    const height: number = this.bmiForm.get("height").value / 100;

    const result: number =
      Math.round((weight / Math.pow(height, 2)) * 100) / 100;

    this.result = result;
  }
}
